// ^------------------------ footer to buttom ------------------------
.body {

    .body__container {
        height: min(100%, 100vh);
        width: 100vw;
        display: flex;
        flex-direction: column;
        overflow-x: hidden;
    }
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 1920px;
    margin: 0 auto;
    background: var(--background-color--background);
}


// ^------------------------ block container ------------------------

.container {
    max-height: 100%;
    margin: 0 auto;
}




